const { SubMenu } = Menu;

const menuBasicData = [
  {
    name: "Khch Hng",
    to: "/Dasboard/khachhang",
    exact: true,
  },
  {
    name: "Nhn Vin",
    to: "/Dasboard/nhanvien",
    exact: true,
  },
  {
    name: "Cc Loi Chi Ph",
    to: "/Dasboard/chiphi",
    exact: true,
  },
  {
    name: "Hng Tu",
    to: "/Dasboard/hangtau",
    exact: true,
  },
  {
    name: "Nh Xe",
    to: "/Dasboard/nhaxe",
    exact: true,
  },
  {
    name: "i L",
    to: "/Dasboard/daily",
    exact: true,
  },
];

const menuQuanLyHoSo = [
  {
    name: "To Phiu Theo Di",
    to: "/Dasboard/taophieutheodoi",
    exact: true,
  },
  {
    name: "To Job Order",
    to: "/Dasboard/taojoborder",
    exact: true,
  },
  {
    name: "To Job Book Tu",
    to: "/Dasboard/taojobbooktauf",
    exact: true,
  },
  {
    name: "Duyt Job Order",
    to: "/Dasboard/duyetjoborder",
    exact: true,
  },
  {
    name: "Bo Biu H S",
    to: "/Dasboard/baobieuhoso",
    exact: true,
  },
];

const menuQuanLyThuChi = [
  {
    name: "Phiu Chi Tm ng",
    to: "/Dasboard/phieuchitamung",
    exact: true,
  },
  {
    name: "Yu Cu Thanh Ton",
    to: "/Dasboard/yeucauthanhtoan",
    exact: true,
  },
  {
    name: "Duyt Thanh Ton",
    to: "/Dasboard/duyetthanhtoan",
    exact: true,
  },
  {
    name: "Tra Cu D Liu",
    to: "/Dasboard/tracuudulieu",
    exact: true,
  },
  {
    name: "Chi Ph Cc Tu/Cont",
    to: "/Dasboard/chiphicuoctaucont",
    exact: true,
  },
]

export default function SiderComponent({ routes }) {
  let history = useHistory();

  const [state, setState] = useState(false);

  const onCollapse = (collapsed) => {
    setState(collapsed);
  };

  const MenuItem = (menus) => {
    var result = null;
    if (routes.length > 0) {
      result = menus.map((menu) => {
        return (
          <Menu.Item key={menu.to}>
            <span>{menu.name}</span>
            <Link to={menu.to} />
          </Menu.Item>
        );
      });
    }
    return result;
  };

  return (
    <Sider collapsible collapsed={state} onCollapse={onCollapse}>
      <div className="logo" />
      <Menu
        theme="dark"
        defaultOpenKeys={["sub1"]}
        defaultSelectedKeys={[history.location.pathname]}
        mode="inline"
      >
        <SubMenu key="sub1" icon={<DatabaseOutlined />} title="D Liu C Bn">
          {MenuItem(menuBasicData)}
        </SubMenu>

        <SubMenu
          key="sub2"
          icon={<UnorderedListOutlined />}
          title="Qun L H S"
        >
          {MenuItem(menuQuanLyHoSo)}
        </SubMenu>
        <SubMenu key="sub3" icon={<FileOutlined />} title="Qun L Thu Chi">
         {MenuItem(menuQuanLyThuChi)}
        </SubMenu>
      </Menu>
    </Sider>
  );
}
